<template>
  <div class="newaddWrap">
      <header>
          <router-link to="/chooseaddress">
          <i class="el-icon-arrow-left"></i>
          </router-link>
          <div>新增地址</div>
      </header>
      <section class="adddetail">
          <div class="form">
              <section class="ui-padding-block">
                  <div class="input-new">
                      <input type="text" placeholder="请填写你的姓名" v-model="name">
                  </div>
                  <router-link to="/searchaddress">
                      <div class="input-new">
                      <input type="text" placeholder="小区/写字楼/学校等" v-model="address">
                      </div>
                  </router-link>
                  <div class="input-new">
                      <input type="text" placeholder="请填写详细送餐地址" v-model="xxAddress">
                  </div>
                  <div class="input-new">
                      <input type="text" placeholder="请填写能够联系到您的手机号" v-model="phone">
                  </div>
                  <div class="input-new">
                      <input type="text" placeholder="备用联系电话（选填）" v-model="phone_bk">
                  </div>
              </section>
              <section class="addbutton"><button @click="addAddress">新增地址</button></section>
          </div>
      </section>
  </div>
</template>

<script>
export default {
name:'NewAddress',
data(){
    return{
        address:'',
        user_id:'',
        xxAddress:'',
        geohash:'',
        name:'',
        phone:'',
        phone_bk:'',
        sex:1,
        tag_type:2,
        tag:'null',
    }
},
created(){
    this.address = localStorage.getItem("Address");
    this.user_id = localStorage.getItem("userid");
    let placeNameHistory = localStorage.getItem("placeNameHistory");
    let hisArr = JSON.parse(localStorage.getItem("placeHistory"));
    let newArr = hisArr.filter(v => {
      return v.name == placeNameHistory;
    });
    this.geohash = newArr[0].geohash;
},
methods:{
    addAddress(){
        this.axios.post("/v1/users/"+this.user_id+"/addresses",{
            user_id:this.user_id,
            address:this.address,
            address_detail:this.xxAddress,
            geohash:this.geohash,
            name:this.name,
            phone:this.phone,
            tag:this.tag,
            sex:this.sex,
            phone_bk:this.phone_bk,
            tag_type:this.tag_type
        }).then(res=>{
            console.log(res)
        }).catch(err => {})
        // this.$router.push('/chooseaddress')
    }
}
}
</script>

<style scoped>
a{
    text-decoration: none;
}
.newaddWrap{
    padding-top: 1.95rem;
}
header>a{
  position: absolute;
  left: 0.4rem;
  color: #fff;
  font-size: 1rem;
  top: 50%;
  transform: translateY(-50%);
  text-decoration: none;
}
header>div{
    width: 50%;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    width: 50%;
    color: #fff;
    text-align: center;
    font-size: .8rem;
    font-weight: 700;
}
.adddetail{
    margin-top: .4rem;
}
.form{
    display: block;
    margin-top: 0em;
}
.ui-padding-block{
    background: #fff;
    padding-top: .4rem;
}
.input-new{
    padding-bottom: .4rem;
}
.ui-padding-block input{
    display: block;
    width: 15rem;
    font-size: .6rem;
    margin: 0 auto;
    padding: .3rem;
    background: #f2f2f2;
    border: 1px solid #ddd;
    border-radius: 3px;
    outline: none;
}
.addbutton{
    margin: .6rem auto;
    width: 15rem;
    background: #4cd964;
    border-radius: 3px;
}
.addbutton button{
    width: 100%;
    font-size: .6rem;
    color: #fff;
    line-height: 1.6rem;
    background: none;
    font-weight: 700;
}
</style>